import vClickOutside from 'v-click-outside'
const initialData = {
  title: '',
  description: ''
}
export default {
  name: 'CreatePostModal',
  directives: {
    clickOutside: vClickOutside.directive
  },
  props: {
    type: {
      type: String,
      default: 'Video'
    }
  },
  data() {
    return {
      errors: {...initialData},
      form: {...initialData},
      loading: false
    }
  },
  methods: {
    onClickOutside() {
      this.$emit('close')
    },
    async submit(data) {
      this.loading = true
      const url = `minitutor/request-${this.type.toLowerCase()}s`
      try {
        const { id } = await this.$axios.$post(url, data)
        this.$router.push(`/dashboard/minitutor/${this.type.toLowerCase()}s/${id}`)
      } catch (e) {
        const { errors, message } = this.$errorResponse(e)
        this.errors = {
          ...initialData,
          ...errors,
        }
        if (message) {
          this.$toast.danger(message)
        }
        if (!Object.keys(errors).length && !message) {
          this.$toast.danger(this.$errorMessage(e))
        }
        this.loading = false
      }
    }
  }
}
